.chatInput{
    border-radius:20px;
   
}
.chatInput >form{
    position:relative;
    display:flex;
    justify-content:center;
}
.chatInput >form >input{
    position:fixed;
    bottom:30px;
    width:70%;
    border:1px solid gray;
    border-radius:3px;
    padding:20px;
    border: none;
    background-color:lightgray;
    
    
}
.chatInput >form >input:focus {
    outline-width: 0;
}
.chatInput >form >button {
    display:none !important;
}
