.chat{
    display:0.7;
    flex-grow:1;
    overflow:scroll;
    padding-bottom:150px;
}
.chat_header{
    display:flex;
    align-items:center;
    justify-content:space-between;
    padding:20px;
    border-bottom:1px solid lightgray;
}
.chat_headerRight >p{
  display:flex;
  align-items:center;
  font-size:14px;
}
.chat_channelName{
    display:flex;
    text-transform:lowercase;
}
.chat_channelName>.MuiSvgicon{
    margin-left:10px;
    font-size:18px;
}
.chat_headerRight >p>.MuiSvgicon-root{
    margin-right:5px !important;
    font-size:16px;
}

